import { useEffect, useState } from "react";
import { Modal } from "antd";
import { getStatus, getStatusClass, convertDuration } from "../utils";
import { useParams } from "react-router-dom";
const { ethers } = require("ethers");
const { confirm } = Modal;
function LoanDetail({ writeContracts, tx, provider, balance }) {
  const { loanId } = useParams();
  const [data, setData] = useState();

  useEffect(() => {
    const getloanById = async id => {
      const res = await writeContracts?.NftFiLoanBank?.getLoanDetail(id);
      if (res) {
        const nft = await fetch(`https://testnets-api.opensea.io/api/v1/asset/${res.tokenAddress}/${res.tokenID}`).then(
          x => x.json(),
        );
        setData({
          image: `https://ipfs.io/ipfs/${nft?.image_original_url?.split("ipfs://")?.[1]}`,
          title: nft?.asset_contract?.name,
          tokenAddress: res.tokenAddress,
          tokenID: res.tokenID.toString(),
          amount: res.amount.toString(),
          repayAmount: res.repayAmount.toString(),
          duration: res.duration.toString(),
          owner: res.tokenOwnerAddress,
          status: res.status.toString(),
        });
      }
    };
    getloanById(loanId);
  }, []);

  const openModal = () => {
    confirm({
      title: "LEND FUNDS",
      content: "Do you Want to lend funds on this Loan Request?",
      okText: "Confirm",
      closeIcon: <></>,
      cancelText: <>Cancel</>,
      onOk() {
        handleOnFireRequest();
      },
    });
  };
  const handleOnFireRequest = async () => {
    await tx(writeContracts.NftFiLoanBank.bidLoan(Number(loanId), { value: ethers.BigNumber.from(data.amount) }));
  };
  if (!data) {
    return <>loading...</>;
  }

  return (
    <>
      <div>
        <div className={`status ${getStatusClass(data.status)}`}>{getStatus(data.status)}</div>
      </div>
      <div className="my-2">LoanTermId: {loanId || "-"}</div>
      <div className="text-base font-semibold my-4">LOAN REQUEST TERMS</div>
      <div className="flex justify-between pr-6">
        <div className="flex flex-col justify-center">{data.owner || "-"}</div>
        <div className="flex flex-col justify-center break-words">
          <h3>{ethers.utils.formatEther(data.amount)} ETH</h3>
          <span>Principal</span>
        </div>
        <div className="flex flex-col justify-center break-words">
          <h3>{ethers.utils.formatEther(data.repayAmount)} ETH</h3>
          <span>Repayment</span>
        </div>
        <div className="flex flex-col justify-center">
          <h3>{data.duration ? convertDuration(data.duration) : "-"} days</h3>
          <span>Duration</span>
        </div>
        <button onClick={openModal} className="btn btn-primary h-11">
          Lend at these terms
        </button>
      </div>
      <div className="text-base font-semibold my-4">LOAN COLLATERAL</div>
      <div className="w-20">
        <div className="card">
          <div className="image-wrapper">
            <img src={data.image} alt="" />
          </div>
          <div className="card-info">
            <h3>{data.title}</h3>
            <div>#{data.tokenID}</div>
            <a
              href={`https://testnets.opensea.io/assets/${data.tokenAddress}/${data.tokenID}`}
              target="_blank"
              rel="noreferrer"
              className="text-blue"
            >
              View on OpenSea
            </a>
          </div>
        </div>
      </div>
    </>
  );
}

export default LoanDetail;
