export const displayAddress = address => {
  return address?.substr(0, 5) + "..." + address?.substr(-4);
};

/**
  0: "unbid",
  1: "bid",
  2: "canceled",
  3: "expired",
  4: "repaid",
  5: "defaulted",
 */
const Status = {
  0: "Unmatched",
  1: "Active",
  2: "Canceled",
  3: "Expired",
  4: "Repaid",
  5: "Defaulted",
};
export const getStatus = statusId => {
  return Status[statusId];
};

export const getStatusClass = status => {
  switch (status) {
    case "1":
      return "green";
    case "3":
    case "5":
      return "red";
    case "0":
    case "2":
    case "4":
      return "blue";
    default:
      break;
  }
};

export const timeDifference = (current, future) => {
  var msPerMinute = 60 * 1000;
  var msPerHour = msPerMinute * 60;
  var msPerDay = msPerHour * 24;
  var msPerMonth = msPerDay * 30;
  var msPerYear = msPerDay * 365;

  var elapsed = future - current;
  if (elapsed < 0) return null;
  if (elapsed < msPerMinute) {
    return "In " + Math.round(elapsed / 1000) + " seconds";
  } else if (elapsed < msPerHour) {
    return "In " + Math.round(elapsed / msPerMinute) + " minutes";
  } else if (elapsed < msPerDay) {
    return "In " + Math.round(elapsed / msPerHour) + " hours";
  } else if (elapsed < msPerMonth) {
    return "In " + Math.round(elapsed / msPerDay) + " days";
  } else if (elapsed < msPerYear) {
    return "In " + Math.round(elapsed / msPerMonth) + " months";
  } else {
    return "In " + Math.round(elapsed / msPerYear) + " years";
  }
};
export const convertDuration = duration => {
  const days = Number(duration) / (3600 * 24);
  return days < 1 ? 1 : days;
};
