import { useEffect, useState } from "react";
import { displayAddress } from "../utils";
import { getStatus, getStatusClass, timeDifference } from "../utils";
const { ethers } = require("ethers");

function LoansFunded({ writeContracts, tx, address }) {
  const [data, setData] = useState();

  useEffect(() => {
    const getloans = async () => {
      const res = await writeContracts?.NftFiLoanBank?.getLoansByLender(address);
      if (!res) return;

      const doSomethingAsync = async el => {
        const tokenID = el.tokenID.toString();
        const nftData = await fetch(`https://testnets-api.opensea.io/api/v1/asset/${el.tokenAddress}/${tokenID}`).then(
          x => x.json(),
        );
        console.log(res);

        return {
          loanId: el.ID.toString(),
          image: nftData?.image_preview_url,
          title: nftData?.asset_contract?.name,
          amount: el.amount.toString(),
          repayAmount: el.repayAmount.toString(),
          duration: el.duration.toString(),
          owner: el.tokenOwnerAddress,
          status: el.status.toString(),
          endTimestamp: new Date(el.endTimestamp * 1000),
        };
      };
      const f = x => new Promise(resolve => setTimeout(() => resolve(doSomethingAsync(x)), 600));
      let myData = [];
      for (let job of res.map(x => () => f(x))) {
        const item = await job();
        myData.push(item);
      }
      setData(myData);
    };
    getloans();
  }, []);
  if (!data) {
    return <>loading...</>;
  }
  const liqudate = async loanId => {
    await tx(writeContracts.NftFiLoanBank.updateDefaultedLoanStatusByLender(Number(loanId)));
  };
  return (
    <>
      {data && data.length === 0 && <>no data</>}
      {data &&
        data.map(el => {
          const isLoanExpired = el.status === "1" && timeDifference(Date.now(), el.endTimestamp) === null;
          return (
            <>
              <div className="list grid grid-cols-6 gap-4 row-span-full">
                <div className="image-wrapper flex align-center">
                  <img src={el.image} alt="" />
                </div>
                <div className="flex flex-col justify-center">
                  <h3>{el.title}</h3>
                  By: {el.owner ? displayAddress(el.owner) : "-"}
                  <br />
                  LoanTermId: {el.loanId || "-"}
                </div>
                <div className="flex flex-col justify-center">
                  <div className={`status ${getStatusClass(el.status)}`}>
                    {isLoanExpired ? "Expired" : getStatus(el.status)}
                  </div>
                  {isLoanExpired && (
                    <button onClick={() => liqudate(el.loanId)} className="btn btn-primary mt-3">
                      Liqudate
                    </button>
                  )}
                </div>
                <div className="flex flex-col justify-center break-words">
                  <h3>{ethers.utils.formatEther(el.amount)} ETH</h3>
                  <span>Principal</span>
                </div>
                <div className="flex flex-col justify-center break-words">
                  <h3>{ethers.utils.formatEther(el.repayAmount)} ETH</h3>
                  <span>Repayment</span>
                </div>
                <div className="flex flex-col justify-center">
                  <h3>{timeDifference(Date.now(), el.endTimestamp) || "--"}</h3>
                  <span>Due Date</span>
                </div>
              </div>
            </>
          );
        })}
    </>
  );
}

export default LoansFunded;
