import { Row, Col } from "antd";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";

function MyWallet({ address }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const history = useHistory();

  useEffect(() => {
    const getAccountNFTList = async address => {
      if (!address) return;
      const data = await fetch(`https://testnets-api.opensea.io/api/v1/assets?owner=${address}&offset=0&limit=40`)
        .then(x => x.json())
        .then(res => {
          console.log(res);
          return res?.assets
            ?.filter(el => el.asset_contract.schema_name === "ERC721")
            ?.map(el => ({
              title: el.collection.name,
              image: el.image_preview_url,
              address: el.asset_contract.address,
              tokenId: el.token_id,
            }));
        });
      if (data) setData(data);
      setLoading(false);
    };
    getAccountNFTList(address);
  }, []);

  const handleOnClick = item => {
    history.push(`/wallet/${item.address}/${item.tokenId}`);
  };
  if (loading) {
    return <>loading...</>;
  }
  if (data?.length === 0) {
    return <>no Asset</>;
  }
  return (
    <>
      <Row gutter={[36, 36]}>
        {data &&
          data.map(el => (
            <>
              <Col xs={{ span: 12 }} md={{ span: 8 }} lg={{ span: 6 }} key={el.address}>
                <div className="card animation" onClick={() => handleOnClick(el)}>
                  <div className="image-wrapper">
                    <img src={el.image} alt="" />
                  </div>
                  <div className="card-info text-center">
                    <h3>{el.title}</h3>
                    <span>#{el.tokenId}</span>
                  </div>
                </div>
              </Col>
            </>
          ))}
      </Row>
    </>
  );
}

export default MyWallet;
