import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { getStatus, getStatusClass, timeDifference, convertDuration } from "../utils";
const { ethers } = require("ethers");

function LoansRequest({ writeContracts, tx, provider, address }) {
  const history = useHistory();
  const [data, setData] = useState();

  useEffect(() => {
    const getloans = async () => {
      const res = await writeContracts?.NftFiLoanBank?.getLoansByAsker(address);
      if (!res) return;
      const doSomethingAsync = async el => {
        const tokenID = el.tokenID.toString();
        const nftData = await fetch(`https://testnets-api.opensea.io/api/v1/asset/${el.tokenAddress}/${tokenID}`).then(
          x => x.json(),
        );
        return {
          loanId: el.ID.toString(),
          image: nftData?.image_preview_url,
          title: nftData?.asset_contract?.name,
          amount: el.amount.toString(),
          repayAmount: el.repayAmount.toString(),
          duration: el.duration.toString(),
          owner: el.tokenOwnerAddress,
          status: el.status.toString(),
          endingBidTimestamp: el.endingBidTimestamp ? new Date(el.endingBidTimestamp * 1000) : null,
          endTimestamp: el.endTimestamp ? new Date(el.endTimestamp * 1000) : null,
        };
      };
      const f = x => new Promise(resolve => setTimeout(() => resolve(doSomethingAsync(x)), 800));
      let myData = [];
      for (let job of res.map(x => () => f(x))) {
        const item = await job();
        myData.push(item);
      }
      setData(myData);
    };
    getloans();
  }, []);
  const cancelRequest = async loanId => {
    await tx(writeContracts.NftFiLoanBank.cancelLoan(Number(loanId)));
  };
  const getBackNft = async loanId => {
    await tx(writeContracts.NftFiLoanBank.updateExpiredLoanStatusByBorrower(Number(loanId)));
  };

  if (!data) {
    return <>loading...</>;
  }

  return (
    <>
      {data && data.length === 0 && <>no data</>}
      {data &&
        data.map(el => {
          console.log(el.endingBidTimestamp);
          const isLoanExpired = el.status === "1" && timeDifference(new Date(), el.endTimestamp) === null;
          const isBidExpired = el.status === "0" && el.endingBidTimestamp.getTime() - new Date().getTime() < 0;
          const isCancel = el.status === "0" && el.endingBidTimestamp.getTime() - new Date().getTime() > 0;
          return (
            <>
              <div className="list grid grid-cols-6 gap-4 row-span-full">
                <div className="image-wrapper flex align-center">
                  <img src={el.image} alt="" />
                </div>
                <div className="flex flex-col justify-center">
                  <h3>{el.title}</h3>
                  LoanTermId: {el.loanId || "-"}
                </div>
                <div className="flex flex-col justify-center">
                  <div className={`status ${getStatusClass(el.status)}`}>
                    {isLoanExpired ? "Expired" : isBidExpired ? "Outdated" : getStatus(el.status)}
                  </div>
                  {isCancel && (
                    <button onClick={() => cancelRequest(el.loanId)} className="btn btn-primary mt-3">
                      Cancel
                    </button>
                  )}
                  {isBidExpired && (
                    <button onClick={() => getBackNft(el.loanId)} className="btn btn-primary mt-3">
                      Take NFT back
                    </button>
                  )}
                </div>
                <div className="flex flex-col justify-center break-words">
                  <h3>{ethers.utils.formatEther(el.amount)} ETH</h3>
                  <span>Principal</span>
                </div>
                <div className="flex flex-col justify-center break-words">
                  <h3>{ethers.utils.formatEther(el.repayAmount)} ETH</h3>
                  <span>Repayment</span>
                </div>
                <div className="flex flex-col justify-center">
                  <h3>{el.duration ? convertDuration(el.duration) : "-"} days</h3>
                  <span>Duration</span>
                </div>
              </div>
            </>
          );
        })}
    </>
  );
}

export default LoansRequest;
