import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { Modal } from "antd";
import { PlusCircleOutlined, LoadingOutlined, CheckCircleOutlined, ClockCircleOutlined } from "@ant-design/icons";
import NFT from "../contracts/erc721abi.json";

const { ethers } = require("ethers");
function NFTDetail({ writeContracts, userSigner, tx }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [isConfirming, setIsConfirming] = useState(false);
  const [isConfirmed, setIsConfirmed] = useState(false);

  const { nftaddress, tokenId } = useParams();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isTermModalVisible, setIsTermModalVisible] = useState(false);

  const [amount, setAmount] = useState("0");
  const [repayAmount, setRepayAmount] = useState("0");
  const [duration, setDuration] = useState("30");

  useEffect(() => {
    const getNFTDetail = async () => {
      await fetch(`https://testnets-api.opensea.io/api/v1/asset/${nftaddress}/${tokenId}`)
        .then(x => x.json())
        .then(res => {
          setData(res);
        });
      setLoading(false);
    };
    getNFTDetail();
  }, []);

  const closeModal = () => {
    setIsModalVisible(false);
  };

  const openModal = () => {
    setIsModalVisible(true);
  };
  const closeTermModal = () => {
    setIsTermModalVisible(false);
  };

  const openTermModal = () => {
    setIsTermModalVisible(true);
  };
  const addAsCollateral = async () => {
    setIsConfirming(true);
    const nftContract = await new ethers.Contract(nftaddress, NFT.abi, userSigner);
    let approveTransaction = await nftContract.approve(writeContracts.NftFiLoanBank.address, Number(tokenId));
    await approveTransaction.wait();
    setIsConfirming(false);
    setIsConfirmed(true);
  };
  const handleProcess = () => {
    closeModal();
    openTermModal();
  };
  const handleOnFireRequest = async () => {
    let endingBidTime = new Date();
    endingBidTime.setMinutes(endingBidTime.getMinutes() + 10);
    setTimeout(() => {
      closeTermModal();
    }, 10000);
    await tx(
      writeContracts.NftFiLoanBank.proposeLoan(
        nftaddress,
        Number(tokenId),
        ethers.utils.parseEther(amount),
        Math.floor(endingBidTime.getTime() / 1000),
        ethers.utils.parseEther(repayAmount),
        Number(duration) * 60 * 60 * 24,
      ),
    );
  };
  if (loading) {
    return <>loading...</>;
  }
  if (data?.length === 0) {
    return <>no Asset</>;
  }

  return (
    <>
      {data && (
        <>
          <div className="flex">
            <div className="image-wrapper w-30 mr-7">
              <img src={data?.image_preview_url} alt="" className="nft-detail-img" />
            </div>
            <div>
              <h3 className="title mb-0">{data?.asset_contract.name}</h3>
              <div className="text-base mb-4">#{tokenId}</div>
              <div className="text-base mb-4">Owner: {data?.owner.address}</div>
              <div>
                <a
                  href={`https://testnets.opensea.io/assets/${data.asset_contract.address}/${data.token_id}`}
                  target="_blank"
                  rel="noreferrer"
                  className="text-blue"
                >
                  View on OpenSea
                </a>
              </div>
              {!isConfirming && !isConfirmed && (
                <button className="btn btn-secondary mt-4" onClick={openModal}>
                  create loan request
                </button>
              )}
            </div>
          </div>
        </>
      )}
      <Modal
        title="Create Loan Request"
        visible={isModalVisible}
        onOk={handleProcess}
        onCancel={closeModal}
        okText={isConfirmed ? "Set Loan Term" : <></>}
        cancelText={<></>}
      >
        <div className="px-4 mb-6">
          Set loan terms for your loan request after confirming your collateral for deposit.
        </div>
        <div className="w-full flex items-center justify-evenly">
          <div className="list-imgWrapper">
            <img src={data.image_preview_url} alt="" />
          </div>
          <div className="p-3">
            <div>
              <h2>{data?.asset_contract.name}</h2>#{tokenId}
            </div>
          </div>
          <div>
            {isConfirming && (
              <button className="btn btn-secondary green flex items-center pointer-events-none">
                <LoadingOutlined />
                &nbsp; Confirming
              </button>
            )}
            {isConfirmed && <CheckCircleOutlined style={{ color: "#18d8a9", fontSize: "32px" }} />}
            {!isConfirming && !isConfirmed && (
              <button className="btn btn-primary flex items-center" onClick={() => addAsCollateral()}>
                <PlusCircleOutlined />
                &nbsp; Confirm
              </button>
            )}
          </div>
        </div>
      </Modal>
      <Modal
        title="Set Loan Terms"
        visible={isTermModalVisible}
        onOk={handleOnFireRequest}
        onCancel={closeTermModal}
        okText={"Finalize Loan Terms"}
        cancelText={<></>}
      >
        <div>How much do you want to borrow</div>
        <div className="px-4 pl-12 py-3 btn-secondary rounded-md my-3 relative">
          <img
            src="https://uploads-ssl.webflow.com/61a98989a418f6f2acefef70/61cd8eb26443af57bccb49ef_985ef003-b1f8-4c98-a482-4469a1e60738.png"
            alt=""
            className="absolute left-3 w-8 h-8"
          />
          <span className="text-lg ml-1">ETH</span>
          <span className="text-2xl ml-3">|</span>
          <input
            className="pl-4 text-lg outline-none w-4/5"
            style={{ backgroundColor: "transparent" }}
            value={amount}
            onChange={e => setAmount(e.target.value)}
          />
        </div>
        <div>Set repayment amount</div>
        <div className="px-4 pl-12 py-3 btn-secondary rounded-md my-3 relative">
          <img
            src="https://uploads-ssl.webflow.com/61a98989a418f6f2acefef70/61cd8eb26443af57bccb49ef_985ef003-b1f8-4c98-a482-4469a1e60738.png"
            alt=""
            className="absolute left-3 w-8 h-8"
          />
          <span className="text-lg ml-1">ETH</span>
          <span className="text-2xl ml-3">|</span>
          <input
            className="pl-4 text-lg outline-none w-4/5"
            type="text "
            style={{ backgroundColor: "transparent" }}
            value={repayAmount}
            onChange={e => setRepayAmount(e.target.value)}
          />
        </div>
        <div>Set loan duration</div>
        <div className="px-4 pl-12 py-3 btn-secondary rounded-md my-3 relative">
          <div className="absolute left-4 top-4 w-8 h-8">
            <ClockCircleOutlined style={{ fontSize: "22px", color: "#387ff3" }} />
          </div>
          <span className="text-lg ml-1">days</span>
          <span className="text-2xl ml-3">|</span>
          <input
            style={{ backgroundColor: "transparent" }}
            className="pl-4 text-lg outline-none w-4/5"
            value={duration}
            onChange={e => setDuration(e.target.value)}
          />
        </div>
      </Modal>
    </>
  );
}

export default NFTDetail;
